import { Component } from '@angular/core';
import { PageScroll } from 'ng2-page-scroll';

@Component({
  selector: 'splash-page',
  template: `
    <div class="splash-page">
      <h1 class="splash-head animated15 fadeInDown">Michael Ashby</h1>
      <p class="splash-subhead animated15 fadeInUp">
        <a href="#projects">Projects</a> | <a
        href="https://github.com/ashbymichael" target="_blank">Github</a> | <a
        href="https://linkedin.com/in/ashbymichael" target="_blank">LinkedIn</a> |
        <a href="#about">About</a>
      </p>
    </div>
  `,
  directives: [PageScroll]
})

export class SplashPage {}



