import { Component } from '@angular/core';
import { ContentDrawer, SplashContainer } from './containers';
import '../../public/css/styles.css';
import '../../public/css/animate.css';

@Component({
  selector: 'app',
  template: `
    <main>
      <a name="top"></a>
      <splash-container></splash-container>
      <content-drawer></content-drawer>
    </main>
  `,
  directives: [SplashContainer, ContentDrawer]
})

export class AppComponent { }



