import { Component } from '@angular/core';

@Component({
  selector: 'about-ribbon',
  template: `
    <div class="ribbon about-ribbon">
    <a name="about"></a>
      <p>
        Michael is a self-driven, collaborative software developer with experience building quality MVC web applications and excellent communication skills. Passionate about clean, tested, maintainable code and building thoughtful, elegant UIs.
      </p>
      <div class="about-img">
        <img src="../../../public/img/michael.jpg" />
      </div>
      <div><a href="#top"><i class="fa fa-2x fa-angle-double-up" aria-hidden="true"></i></a></div>
    </div>
  `
})

export class AboutRibbon {}



