import { Component, Input, OnInit } from '@angular/core';
import { ProjectLink } from './project-link';

@Component({
  selector: 'ribbon',
  template: `
    <div class="ribbon {{cssClass}}">
      <div class="project-info">
        <h3>{{project.title}}</h3>
        <p>{{project.description}}</p>
        <ul>
          <li *ngFor="let item of project.body" [innerHTML]="item"></li>
        </ul>
        <p>
          <project-link
            *ngFor="let link of project.links"
            [link]="link"
            class="project-link"
          >
          </project-link>
        <p>
      </div>
      <div class="img">
        <img src="{{project.img}}" />
      </div>
    </div>
  `,
  directives: [ProjectLink]
})

export class Ribbon implements OnInit {
  @Input() project = {};
  @Input() i: number;

  cssClass: string = "ribbon-dark";

  ngOnInit() {
    if (this.i % 2 !== 0) {
      this.cssClass = "ribbon-light";
    }
  }
}



