import { Injectable } from '@angular/core';

@Injectable()
export class ProjectService {
  projects: any[] = [
    {
      title: 'Kaplan International Berkeley Library System',
      description: 'Library Management System built for the Berkeley branch of Kaplan International Colleges.',
      body: [
        'Increased library budget by ~15% by eliminating loss through designing library circulation management tool with Ruby on Rails for language school to organize their physical library used by 200+ students and 10 staff.',
        'Designed streamlined checkout system UI/UX using JavaScript, jQuery, AJAX, and PureCSS which reduced book checkout transaction time by ~75%.',
        'Engineered PostgreSQL DB schema using ActiveRecord to store 1,200+ books with the features to import and export data from pre-existing databases and create custom reports (CSV and PDF).',
        'Collected and incorporated feedback from 30% of userbase to refactor features to improve UX and workflow.',
        'Achieved 100% coverage of codebase by following TDD and writing robust test suite with RSpec and Capybara.'
      ],
      links: [
        {href: 'https://github.com/ashbymichael/ki-lib', text: 'Github'},
        {href: 'http://kiclibdemo.herokuapp.com', text: 'Heroku'}
      ],
      img: '../../../public/img/kicliba.png'
    },
    {
      title: 'StudyCard',
      description: 'Flashcard-based Spaced Repetition System study tool for memorization',
      body: [
        'Maximized average user retention rates after studying cards to ~92% and reduced time spent building and organizing decks to 0 by implementing <a href="https://www.supermemo.com/english/ol/sm2.htm" target="_blank">SuperMemo2 algorithm</a> in Ruby.',
        'Built responsive interface using jQuery, CSS and ERB',
        'Designed and implemented PostgreSQL database to organize cards and decks.'
      ],
      links: [
        {href: 'https://github.com/ashbymichael/studycard3', text: 'Github'},
        {href: 'http://studycard2016.herokuapp.com', text: 'Heroku'}
      ],
      img: '../../../public/img/studycarda.png'
    },
    {
      title: 'Benkyo',
      description: 'Update to StudyCard based on Angular2.',
      body: [
        'Converted existing Rails backend into a RESTful JSON API to accomodate decoupled front-end applications.',
        'Reduced HTTP requests to Rails backend by ~75% through utilizing Angular 2.0 to handle page routing.'
      ],
      links: [
        {href: 'https://github.com/ashbymichael/benkyo3', text: 'Github'}
      ],
      img: '../../../public/img/benkyo.png'
    },
    {
      title: 'Privately Owned Public Open Spaces',
      description: 'Visual map for urban park lovers to discover nearby Privately-Owned Public Open Spaces (POPOS) in San Francisco.',
      body: [
        'Wrote CSV parser using Ruby to organize raw location data of 74+ POPOS from SF OpenData into hashmaps.',
        'Designed UI/UX using Material Design Lite CSS to complement existing GoogleMaps API style themes.',
        'Integrated the GoogleMaps API to show detailed POPOS card information (park times, bathrooms, parking).',
        'Resolved geolocation issues by developing algorithms in Ruby for locating and sorting nearby GPS data.'
      ],
      links: [
        {href: 'https://github.com/ashbymichael/popos', text: 'Github'},
        {href: 'http://sf-popos.herokuapp.com', text: 'Heroku'}
      ],
      img: '../../../public/img/popos_1.png'
    },
    {
      title: 'Codex',
      description: 'CODEX is an automated studying tool that encourages users to read more code through lesson plan creation and frequent reminders.',
      body: [
        'Selected Github repositories for study based on user search terms and repo criteria such as popularity, number of contributors and frequency of activity.',
        'Delivered study plans via SMS message or email utilizing the Twilio and SendGrid APIs.',
        'Utilized Google Fonts, Materialize CSS, Bootstrap and jQuery to create dynamic, responsive front-end UI/UX.'
      ],
      links: [
        {href: 'https://github.com/ashbymichael/codexproject', text: 'Github'},
        {href: 'http://teamcodex.herokuapp.com', text: 'Heroku'}
      ],
      img: '../../../public/img/codex.png'
    }
  ];

  getProjects() {
    return this.projects;
  }
}



