import { Component } from '@angular/core';
import { ArrowRibbon, Ribbon, Placeholder, AboutRibbon } from '../components';
import { ProjectService } from '../services/project';

@Component({
  selector: 'content-drawer',
  template: `
    <div class="content-drawer">
      <arrow-ribbon></arrow-ribbon>
      <ribbon
        [project]="project"
        [i]="i"
        *ngFor="let project of projects, let i = index"
      ></ribbon>
      <about-ribbon></about-ribbon>
    </div>
  `,
  directives: [ArrowRibbon, Ribbon, Placeholder, AboutRibbon],
  providers: [ProjectService]
})

export class ContentDrawer {
  projects: any[] = [];

  constructor(private projectService: ProjectService) {
    this.projects = this.projectService.getProjects();
  }


}



